@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=block);
body {
  margin: 0 auto;
  background: #303030;
  font-family: "Source Sans Pro", sans-serif;
  font-display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (min-width: 560px) {
  body {
    width: 560px;
  }
}

.App-container{display:grid;width:100%;height:100vh;height:calc(1vh * 100);height:calc(var(--vh, 1vh) * 100);background:#fff;grid-template-areas:"nav" "main" "foot";grid-template-columns:1fr}.App-container nav{grid-area:nav}.App-container main{grid-area:main}.App-container footer{grid-area:foot}
